    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-image: url('./paper.jpg');
  background-size: cover;
  background-repeat: no-repeat; */
